import { useNavigate, useParams } from "react-router-dom";
import { useLayoutEffect, useRef, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { deleteMemo } from "../store";
import * as DOMPurify from "dompurify";
import notContent from "../imgs/not-group.png";
import "../css/ContentPage.css";
import "../mediaCss/ContentPageMedia.css";

const ContentPage = () => {
  const [menu, setMenu] = useState(false);
  const contentRef = useRef();
  const darkMode = useSelector((state) => state.darkMode);
  const memoList = useSelector((state) => state.memo);
  const [currentItem, setCurrentItem] = useState([]);
  const [nonePage, setNonePage] = useState(false);
  let { id } = useParams();
  let dispatch = useDispatch();
  let navigate = useNavigate();

  useLayoutEffect(() => {
    let currentItem = memoList.find((memo) => memo.id == id);
    if (currentItem !== undefined) {
      setCurrentItem(currentItem);
      setNonePage(false);
    }

    if (currentItem === undefined) {
      setNonePage(true);
    }
  }, [id, memoList]);

  return (
    <div
      className="ContentPage"
      onClick={(e) => {
        if (e.target !== contentRef.current) {
          setMenu(false);
        }
      }}
    >
      {nonePage ? (
        <div className="none-content">
          <img src={notContent} alt="  " />
          <p className="none-text">   </p>
          <p>   .</p>
        </div>
      ) : (
        <div className="main-content">
          <h2>{currentItem.title}</h2>
          <div className="tag">
            <p>{currentItem?.subTitle}</p>
            <div className="menu-container">
              <div
                className="menu-icon"
                style={{ color: darkMode ? "white" : "" }}
                onClick={(e) => {
                  e.stopPropagation();
                  setMenu(!menu);
                }}
              >
                . . .

                {menu ? (
                <>
                  <div className="menu" style={{
                    position : "absolute",
                    top : "32px",
                    left : "-13px"
                  }}>
                    <p
                      className="delete"
                      onClick={(e) => {
                        let result =
                          window.confirm("  ?");
                        if (result) {
                          dispatch(deleteMemo(currentItem.id));
                          alert("");
                          navigate("/");
                        }
                        if (!result) {
                          alert("");
                          setMenu(false);
                        }
                      }}
                      style={{
                        color : darkMode ? "black" : null
                      }}
                    >
                      
                    </p>

                    <p
                      className="modify"
                      onClick={() => {
                        navigate(`/modify/${currentItem.id}`);
                      }}
                      style={{
                        color : darkMode ? "black" : null
                      }}
                    >
                      
                    </p>
                  </div>
                </>
              ) : null}
              </div>
            </div>
          </div>
          <div
            className="viwer"
            style={{
              border: darkMode ? "1px solid white" : "1px solid black",
            }}
            dangerouslySetInnerHTML={{
              __html: DOMPurify.sanitize(currentItem.content),
            }}
          ></div>
        </div>
      )}
    </div>
  );
};

export default ContentPage;
